const mongoose = require('mongoose');

let Schema = mongoose.Schema;

let userSchema = new mongoose.Schema({
    email: { 
        type: String,
        required: true,
        unique: true 
    },
    password: {
        type: String,
        required: true,
    },
    username:{
        type: String,
        required: true,
        unique: true  
    }

})


module.exports = mongoose.model('User', userSchema);



